import React from 'react';
import CardItem from './CardItem';
import './Cards.css'

function Cards() {
    return (
        <div className="cards">
            <h1>Только посмотри на эти НЕЗАБЫВАЕМЫЕ локации!</h1>
            <div className="cards__container">
                <div className="cards__wrapper">
                    <ul className="cards__items">
                        <CardItem
                            src="images/img-9.jpg"
                            text="Исследуй водопад, скрытый глубоко внутри джунглей Амазонки"
                            label="Приключения"
                            path="/services"
                        />
                        <CardItem
                            src='images/img-2.jpg'
                            text='Путешествуй по островам Бали в частном круизе'
                            label='Роскошь'
                            path='/services'
                        />
                    </ul>
                    <ul className='cards__items'>
                        <CardItem
                            src='images/img-3.jpg'
                            text='Отправься под парусом на неисследованые острова Атлантики'
                            label='Загадка'
                            path='/services'
                        />
                        <CardItem
                            src='images/img-4.jpg'
                            text='Сыграй в футбол на вершинах Гималаев'
                            label='Приключения'
                            path='/products'
                        />
                        <CardItem
                            src='images/img-8.jpg'
                            text='Пересеки Сахару на верблюдах'
                            label='Адреналин'
                            path='/sign-up'
                        />
                    </ul>
                </div>
            </div>
        </div>
    )
}

export default Cards